import { CSSProperties, FC } from 'react';
import { TwBaseProps } from '../../types';
import {
  Card as MantineCard,
  CardProps as MantineCardProps,
  CardSectionProps as MantineCardSectionProps,
} from '@mantine/core';
import { PropsFrom } from '../../utility-types';

export interface CardProps extends TwBaseProps, MantineCardProps {
  overflow?: CSSProperties['overflow'];
}

interface ICard extends FC<CardProps> {
  Section: FC<PropsFrom<typeof MantineCard.Section> & ICardSection>;
}

// TODO: See what to omit from here
interface ICardSection extends MantineCardSectionProps {
  withTopBorder?: boolean;
  withBottomBorder?: boolean;
}

// TODO: See what to do about forwarding refs here
const Root: FC<MantineCardProps> = (props) => <MantineCard {...props} />;

export const Card: ICard = (props) => {
  const { overflow = 'visible', ...other } = props;
  return <Root {...other} style={{ overflow }} />;
};

Card.Section = ({ withBottomBorder, withTopBorder, ...props }: ICardSection) => {
  // TODO: Extract function itself or in separate class?
  return (
    <MantineCard.Section
      {...props}
      style={(() => {
        const styles: CSSProperties = {};
        if (!props.withBorder) return styles;

        if (withTopBorder === false) styles.borderTopWidth = 0;
        if (withBottomBorder === false) styles.borderBottomWidth = 0;

        return styles;
      })()}
    />
  );
};
